:root{
  --white:#ecf0f1;
  --black:#34495e;
  --grey:#bdc3c7;
  --green:#1abc9c;
}

*{
  font-family: 'Roboto', sans-serif;
}

/* Nav Menu ======================================*/

nav{
  background-color:none;
  box-shadow: 0px 3px 0px 0px var(--grey);
  font-size:1.3rem;
  width: 100%;
}
.container-nav{
  display: flex;
  justify-content: left;
  text-decoration: none;
  align-items:center;
  padding:0px 70px;
}
.container-nav a{
  text-decoration:none;
  color: var(--black);
  transition: .3s;
}
.container-nav a:hover{
  color: var(--green);
}
.navMenu ul{
  display: flex;
}
.navMenu ul li{
  list-style: none;
  margin:0px 20px;
}

/* END NAV====================================== */

/* HOME ======================================== */

.container-home{
  width:100%;
  background-color: none;
}
/* End Home */


/* Head============================================ */
.container-head{
  width: 100%;
  height:91vh;
  background-image: url("./img/11.jpg");
  background-repeat: no-repeat;
  background-size:cover;
  background-position:top;
}
.content-head{
  color: var(--white);
  padding:30px 70px;
  text-align:center;
}

.text-head{
  margin-top:15%;
  color: var(--white);
}
.text-head h1{
  text-transform: uppercase;
  font-size:3rem;
  line-height:0px;
}
.text-head p{
  font-size:2rem;
}
.text-head button{
  padding:15px 40px;
  border:none;
  background-color: var(--green);
  color: var(--white);
  cursor: pointer;
  font-size: 1.2rem;
}


/* End Head ======================================== */

/* Start Service */

.container-service{
  width:100%;
  
}
.content-service{
  padding:30px 70px;
  color:var(--black);
  text-align:center;
}
.content-service h1{
  line-height:10px;
}

.border{
  width: 100px;
  height: 5px;
  background-color: var(--green);
  margin:auto;
}

.feature-service{
  display: flex;
  justify-content: center;
  padding:50px 0px;
}

.card{
  width:30%;
  text-align:center;
  margin:0px 10px;
  color: var(--black);
}
.card i{
  font-size:2rem;
  background-color: var(--green);
  padding:15px 15px;
  border-radius:50%;
  color: var(--white);
}
.text-card{
  padding:10px;
  margin-top:-20px;
  background-color: var(--white);
}
.text-card p{
  line-height:25px;
}

/* End Service */

/* Start Galleries =================================*/

.container-galleries{
  width: 100%;
  background-image: url("./img/13.jpg");
  background-repeat: no-repeat;
  background-size:cover;
  background-position:center;
  text-align:center;
}
.content-galleries{
  padding:15vh 0px;
}
.content-galleries button{
  padding:25px 100px;
  border:none;
  background-color:var(--green);
  cursor: pointer;
  font-size: 2rem;
  color: var(--white);
  margin:auto;
  align-items:center;
}

/* End Galleries ===================================*/

/* Start Gallery ====================================*/

.container-gallery{
  width:100%;
}

.content-gallery{
  padding:30px 70px;
  margin:auto;

}

.gallery h1{
  line-height:0px;
  color: var(--black);
  text-align:center
}

.border-gallery{
  width: 50px;
  height:5px;
  background-color:var(--green);
  line-height: 0px;
  margin:auto;
}

.image-gallery{
  padding:20px 0px;
  width: 100%;
}
.column{
  display: flex;
  justify-content: center;
  margin:auto;
}
.column img{
  width:30%;
  margin:10px;
}

/* End Gallery =====================================*/

/* Contact ========================================== */

.container-contact{
  width:100%;
}
.content-contact{
  padding:30px 70px;
  color:var(--black);
  text-align:center;
}
.content-contact h1{
  line-height: 10px;
}
.border-contact{
  width:100px;
  height: 5px;
  background-color: var(--green);
  margin:auto;
  line-height: 0px;
}
.display-contact{
  display:flex;
  justify-content: space-between;
  margin: 50px auto;
  width: 90%;
}
.body-contact{
  width: 30%;
}
.icon{
  line-height: 10px;
}
.icon i{
  font-size:2rem;
  color: var(--green);
}
.icon h2{
  font-weight:500;
}
.text a{
  color: var(--black);
  text-decoration: none;
}
/* End Contact ====================================== */



/* Footer========================================== */


  footer{
  width:100%;
  color:var(--white);
  background-color: var(--black);
  box-shadow: 0px -3px 0px 0px var(--green);
  text-align: center;
}
  .container-footer{
  display: flex;
  align-items:none;
  justify-content:space-around;
  padding:0px 70px;
}
  footer ul li{
  list-style: none;

}
  footer h4{
  font-size:20px;
}


.location{
  text-align:left;
  width:25%;
}
.location-text{
  display: flex;
  justify-content: space-around;
}
.location-text i{
  margin-right:10px;
  cursor: pointer;
}
.location-text a{
  text-decoration:none;
  color: var(--white);
  text-align:justify;
  transition: .5s;
}
.location-text a:hover{
  color: var(--green);
}


.service{
  text-align: left;
}
  .service ul{
  margin-left:-40px;
}
  .service li{
  text-align:left;
  margin-bottom:10px;
}
  .service a{
  text-decoration:none;
  color: var(--white);
  transition: .3s;
}
  .service a:hover{
  color: var(--green);
}


  .sosmed ul{
  display: flex;
  margin-left:-40px;
}
  .sosmed li{
  margin-right:20px;
}
  .sosmed i{
  font-size: 1.2rem;
  cursor: pointer;
  transition:.3s;
  color: var(--white);
}
  .sosmed i:hover{
  color: var(--green);
}

/* End FOOTER ==================================*/

/* Start Profile =================================== */

.container-profile{
  width: 100%;
  color:var(--black);

}
.content-profile{
  padding:30px 70px;
}
.gress h1{
  line-height: 0px;
}
.border-profile{
  width: 50px;
  height:5px;
  background-color:var(--green);
}

.text-profile{
  display: flex;
  align-items:center;
  justify-content: space-between;
}
.text-profile ul{
  width:40%;
  text-align:justify;
  line-height:25px;
  font-size:1.2rem;

}
.text-profile ul li{
  list-style:none;
  margin-left:-40px;
}

.text-profile img{
  margin:auto;
}

/* End Profile ==================================== */


/* RESPONSIVE ================================= */

@media screen and (max-width:768px){
  /* Navigation */
  .container-nav{
    justify-content: center;
    text-align: center;
  }

  /* Head */
  .text-head{
    margin-top:25%;
  }

  /* Service */
  .content-service{
    padding:30px 0px;
  }
  
  /* Contact */
  .content-contact{
    padding:30px 0px;
  }

  /* Profile */
  .text-profile{
    display: block;
    justify-content: center;
    margin:auto;
  }
  .text-profile ul{
    width: 90%;
    text-align: justify;
    text-justify: auto;

  }
  .text-profile img{
    margin:auto;
    width:100%;
  }

  /* Gallery */
  .content-gallery{
    padding:30px 0px;
  }

  .column img{
    margin:5px;
  }
}

@media screen and (max-width:600px) {
  /* Head */
  .container-head{
    height:70vh;
  }

  .content-head{
    padding:30px 0px;
  }

  .text-head h1{
    font-size:2.5rem;
    letter-spacing: 0px;
  }

  /* Footer */
  .container-footer{
    padding:10px 0px;
  }
}

@media screen and (max-width:480px ){
  /* Navigation */
  .container-nav{
    padding:20px 30px;
    justify-content: space-between;
  } 
  .navMenu{
    align-items: center;
    background-color: var(--green);
    position:absolute;
    top:70px;
    left:0px;
    width:100%;
    display: none;
    
  }
  .navMenu ul{
    display: block;
  }
  .navMenu ul li{
    margin:15px 0px;
  }

  .humburger{
    display: block;
    cursor:pointer;
  }
  .humburger div{
    width: 35px;
    height: 5px;
    background-color: var(--black);
    margin:5px auto;
  }

  .toggle .strip1{
    transform: rotate(-45deg) translate(-5px,9px);
  }
  .toggle .strip2{
    opacity:0;
  }
  .toggle .strip3{
    transform:rotate(45deg) translate(-5px,-9px);
  }
  
  
  
  /* Head */

  .content-head{
    width: 100%;
  }
  .text-head{
    text-align:center;

    margin-top:30%;
  }
  .text-head h1{
    font-size:2rem;
    letter-spacing: normal;
  }
  .text-head p{
    font-size:1rem;
  }

  /* Service */
  .feature-service{
    display: block;
    margin:-30px auto;
  }
  .card{
    margin:10px auto;
    width: 90%;
  }

  /* Galleries */

  /* Contact */
  .display-contact{
    display: block;
    margin:20px auto;
  }
  .body-contact{
    margin:30px auto;
    width: 90%;
  }

  /* Footer */
  .container-footer{
    width:90%;
    display: block;
    margin:auto;
  }

  .location, .service, .sosmed{
    margin:auto;
    text-align:center;
    width: 90%;
  }

  .location a{
    text-align:center;
  }
  .service ul li{
    text-align: center;
  }
  .sosmed ul{
    justify-content: center;
  }
}

@media screen and (max-width:360px){
  /* Head */
  
  .content-head{
    margin:auto;
    text-align:center;
  }
  .text-head{
    margin-top:40%;
  }
  .text-head h1{
    letter-spacing:0px;
    font-size:1.5rem;
  }
  .text-head button{
    padding:10px 25px;
    font-size: 1rem;
  }

  /* Galleries */
  .content-galleries button{
    padding:10px 25px;
    font-size: 1.5rem;
  }

  /* Gallery */
  .content-gallery{
    width: 90%;
    padding:10px;
  }
  .image-gallery{
    margin:auto;
  }
  .column{
    display: block;
  }
  .column img{
    width: 100%;
    margin:5px auto;
  }

  /* Profile */
  .content-profile{
    width: 90%;
    padding:10px;
  }
}